import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { Compensacao } from '../../../../models/dtos/mfp/compensacao';
import { CompensacaoGps } from '../../../../models/dtos/mfp/compensacaoGps';
import GuiaGpsIS from '../../../components/mfp/guiaGpsIS/GuiaGpsIS';

const CompensacaoGpsCompensacaoEditView: FC<DetailEditViewFormProps<CompensacaoGps>> = props => {
  const formProps = useForm<CompensacaoGps>({ defaultValues: { valor: 0, compensar: true, ...props.selectedItem }, mode: 'onBlur' });

  useEffect(
    () => {
      if (!formProps.getValues().nroOrdem) {
        if (instanceOfCompensacaoGps(props.parentFormProps.getValues())) {
          const { guias } = props.parentFormProps.getValues();
          if (guias && guias.length > 0) {
            formProps.setValue('nroOrdem', +guias.reduce((prev, current) => (prev.nroOrdem > current.nroOrdem ? prev : current)).nroOrdem + 1);
          } else {
            formProps.setValue('nroOrdem', 1);
          }
        }
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const instanceOfCompensacaoGps = (object: any): object is Compensacao => {
    return 'guias' in object;
  };
  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <InputGroupInline id="compensar" formProps={formProps} hidden={true} />
      <InputGroupInline id="nroOrdem" formProps={formProps} hidden={true} />
      <GuiaGpsIS id="guia" formProps={{ ...formProps, validation: { required: true } }} label="Guia" />
      <Row>
        <Col>
          <InputGroupInline formProps={formProps} id="valor" isPrependLabel={true} label="Valor" type={InputType.NUMBER} />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default CompensacaoGpsCompensacaoEditView;
