import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/guiaGps';

const GuiaGpsIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'referencia',
            text: 'Referência',
          },
          {
            dataField: 'dtVencimento',
            text: 'Dt. de Vencimento',
          },
          {
            dataField: 'tipo',
            text: 'Tipo',
          },
          {
            dataField: 'vrTotal',
            text: 'Valor Total',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'GuiaGpsSearchView',
      }}
      label={props.label ? props.label : 'Guia Gps'}
      placeholder={props.placeholder ? props.placeholder : 'Informe a Guia GPS'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default GuiaGpsIS;
