import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Label, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertType,
  ColumnsProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  getSelectedItem,
  InputGroupInline,
  InputType,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, Compensacao } from '../../../../models/dtos/mfp/compensacao';
import { EMPREGADO_DOMESTICO } from '../../../../models/enumerated/mfp/categoriaEnum';
import {
  DEC_TERCEIRO_SALARIO_MATERNIDADE,
  NORMAL,
  SALARIO_FAMILIA,
  SALARIO_MATERNIDADE,
} from '../../../../models/enumerated/mfp/tipoCompensacaoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import GuiaGpsIS from '../../../components/mfp/guiaGpsIS/GuiaGpsIS';
import TomadorServicoIS from '../../../components/mfp/tomadorServicoIS/TomadorServicoIS';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';
import CompensacaoGpsCompensacaoEditView from '../compensacaoGpsCompensacao/CompensacaoGpsCompensacaoEditView';

interface CompensacaoEditViewProps extends EditViewProps<Compensacao> {}

const CompensacaoEditView: FC<CompensacaoEditViewProps & RouteComponentProps> = props => {
  const selectedItems = getSelectedItem(props);
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<Compensacao>({ defaultValues: { valor: 0.01, ...selectedItems }, mode: 'onBlur' });
  const [isDeducao, setIsDeducao] = useState<boolean>(false);
  const [faltaCompensar, setFaltaCompensar] = useState();
  const labelWidth = 150;

  useEffect(
    () => {
      formProps.register({ name: 'tipo' });
      const { tipo } = formProps.getValues();
      if (tipo) {
        setIsDeducao(tipo === SALARIO_FAMILIA.value || tipo === SALARIO_MATERNIDADE.value || tipo === DEC_TERCEIRO_SALARIO_MATERNIDADE.value);
      } else {
        formProps.setValue('tipo', NORMAL.value);
      }

      atualizarTotalCompensar();
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const atualizarTotalCompensar = () => {
    let total;
    const { guias, valor } = formProps.getValues();
    if (guias && guias.length > 0) {
      total = valor - guias.filter(it => it.compensar).reduce((sum: number, guiaP) => sum + +guiaP.valor, 0);
    } else {
      total = valor;
    }

    setFaltaCompensar(total);
  };

  useEffect(() => {
    atualizarTotalCompensar();
  });

  const colunasGuiaCompensacao: ColumnsProps[] = [
    {
      dataField: 'guia.referencia',
      text: 'Referência',
    },
    {
      dataField: 'valor',
      text: 'Valor',
    },
    {
      dataField: 'compensacao.observacao',
      text: 'Observação',
    },
  ];

  const guiaCompensacaoFormProps: DetailManagerFormProps = {
    columns: colunasGuiaCompensacao,
    detailView: CompensacaoGpsCompensacaoEditView,
    formProps,
    propName: 'guias',
    title: 'Guias compensadas',
  };

  const apenasDomesticos = () => {
    const { vinculo } = formProps.getValues();
    if (vinculo && vinculo.categoria !== EMPREGADO_DOMESTICO.value) {
      alertManager.emit(ACTION_EVENTS.SHOW, { message: 'Apenas Domésticos são permitidos.', type: AlertType.INFORMATION });
      formProps.setValue('vinculo', null);
    }
  };

  const beforePersist = (formData: Compensacao) => {
    formData.empresaMFP.id = globalParameter.empresaMFP.id;
    formData.empresaMFP.referencia = globalParameter.empresaMFP.referencia;
  };

  const validateExtras = () => {
    const erros = [];

    if (faltaCompensar < 0) {
      erros.push('Total a Compensar não pode ser menor que zero.');
    }

    return erros;
  };

  return (
    <WindowComponent
      title="GPS (Compensação/Dedução)"
      useGlobalParameter={true}
      globalParameter={globalParameter}
      dependencies={[() => hasEmpresa(globalParameter), () => hasEmpresaMFP(globalParameter)]}
      wikiId={892}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} beforePersist={beforePersist} validateExtras={validateExtras}>
        <InputGroupInline id="empresaMFP.id" formProps={formProps} hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Referência"
              type={InputType.REFERENCE}
              tooltipProps={{ message: 'Referência da Guia Paga a maior ou indevida' }}
              disabled={isDeducao}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              formProps={{ ...formProps, validation: { min: 0.01 } }}
              id="valor"
              isPrependLabel={true}
              label="Valor"
              placeholder="Informe o Valor."
              type={InputType.NUMBER}
              disabled={isDeducao}
            />
          </Col>
          <Col style={{ marginTop: '10px' }}>
            <CustomInput type="switch" id="retencaoFonte" name="retencaoFonte" label="Retenção na Fonte" innerRef={formProps.register} />
          </Col>
        </Row>
        <Row>
          <Col>
            <GuiaGpsIS labelWidth={labelWidth} id="guiaGpsGeradora" formProps={formProps} label="GPS Geradora" disabled={isDeducao} />
          </Col>
        </Row>
        <Row>
          <Col>
            <TomadorServicoIS
              labelWidth={labelWidth}
              id="tomador"
              formProps={formProps}
              label="Tomador"
              placeholder="Selecione o Tomador"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
              disabled={isDeducao}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <VinculoIS
              labelWidth={labelWidth}
              formProps={formProps}
              id="vinculo"
              inputSearchProps={{ selectProps: { isMulti: false } }}
              label="Doméstico"
              placeholder="Selecione o Doméstico"
              disabled={isDeducao}
              onChange={apenasDomesticos}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              id="observacao"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { maxLength: 250 } }}
              label="Observação"
              type={InputType.TEXT_AREA}
              placeholder="Informe Uma Observação"
              disabled={isDeducao}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <DetailManagerForm
              {...props}
              {...guiaCompensacaoFormProps}
              onChange={atualizarTotalCompensar}
              keyField="rowIndex"
              hideAddButton={isDeducao}
              hideEditButton={isDeducao}
              hideRemoveButton={isDeducao}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Label className="font-weight-bold">Total a Compensar: {faltaCompensar}</Label>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CompensacaoEditView;
